<script>
import $ from "jquery";
$(document).ready(function () {
  $(".help__title").click(function () {
    $(".help__title").not($(this)).removeClass("active");
    $(".help__content").not($(this).next()).slideUp(300);
    $(".help__content").toggleClass("active");
    $(".help__item").toggleClass("active");
    $(".help__arrow").toggleClass("active");
    $(this).toggleClass("active").next().slideToggle(300);
  });
});
